<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header">
        <!-- 搜索表单 start -->
        <el-form :inline="true" :model="searchValidateForm" ref="searchValidateForm" label-width="100px" class="search-validate-form" :rules="searchValidateFormRules">
          <!-- 输入检验项名称 start -->
          <el-form-item label="" prop="name">
            <el-input v-model="searchValidateForm.name" placeholder="请输入检验项名称"></el-input>
          </el-form-item>
          <!-- 输入检验项名称 end -->

          <!-- 请选择日期统计方式 start -->
          <el-form-item label="" prop="" :rules="[
      { max: 50, message: '景区名称长度不能大于50个字符'}
    ]">
            <el-select v-model="selectDateState" filterable placeholder="请选择日期统计方式" @change="onDateChange">
              <el-option v-for="(item, index) in dateStateOptions" :key="index" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择日期统计方式 end -->

          <!-- 输入起止时间 start -->
          <el-form-item label="" prop="startEndDate">
            <el-date-picker v-model="searchValidateForm.startEndDate" unlink-panels :type="dateType" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期" :value-format="valueFormat">
            </el-date-picker>
          </el-form-item>
          <!-- 输入起止时间 end -->

        </el-form>
        <!-- 搜索表单 end -->

        <!-- 搜索 start -->
        <el-button type="primary" icon="el-icon-search" @click="onSearch('searchValidateForm')">搜索</el-button>
        <!-- 搜索 end -->

        <!-- 重置 start -->
        <el-button type="primary" icon="el-icon-refresh-right" @click="onReset">重置</el-button>
        <!-- 重置 end -->

        <!-- 新增 start -->
        <el-button type="primary" icon="el-icon-circle-plus-outline" @click="onAdd">添加</el-button>
        <!-- 新增 end -->
      </el-header>
      <el-main>
        <!-- 表格 start -->
        <el-table class="table" :data="tableData" style="width: 100%" border v-loading="tableLoading" :header-cell-style="{ background: '#f2f2f2',color: '#606266', height: '38px', padding: '0px' }" :cell-style="{height: '38px',padding: '0'}">
          <el-table-column v-for="(value, key) in tableFields" :key="key" :prop="value" :label="key" min-width="130">
          </el-table-column>
          <el-table-column label="操作" min-width="130" fixed="right">
            <template slot-scope="scope">
              <el-button @click.native.prevent="editRow(scope.$index, scope.row)" type="text" size="small">
                编辑
              </el-button>

              <el-button @click.native.prevent="deleteRow(scope.$index, scope.row)" type="text" size="small">
                删除
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <!-- 表格 end -->
      </el-main>
      <el-footer>
        <!-- 分页 start -->
        <div class="block">
          <el-pagination background layout="prev, pager, next, total, sizes, jumper" :total="100" :current-page="currentPage" :page-sizes="[10, 20, 30, 40, 50, 60, 70, 80, 90]" :page-size="pageSize" @current-change="onCurrentChange" @size-change="onSizeChange">
          </el-pagination>
        </div>
        <!-- 分页 end -->
      </el-footer>

      <!-- 新增 dialog对话框 start -->
      <el-dialog class="dialog" title="新增质检标准" :visible.sync="addDialogVisible" :before-close="addHandleClose" :close-on-click-modal="false">
        <!-- form表单 -->
        <el-form :model="validateForm" ref="validateForm" label-width="100px" class="demo-ruleForm" :rules="validateFormRules">
          <!-- 校验标准名称 start -->
          <el-form-item label="校验标准名称" prop="name">
            <el-input type="text" v-model="validateForm.name" autocomplete="off" placeholder="请输入校验标准名称"></el-input>
          </el-form-item>
          <!-- 校验标准名称 end -->

          <!-- 适用机器型号 start -->
          <el-form-item label="适用机器型号" prop="aplicableModel">
            <el-input type="text" v-model="validateForm.aplicableModel" autocomplete="off" placeholder="请输入适用机器型号"></el-input>
          </el-form-item>
          <!-- 适用机器型号 end -->

          <!-- 校验类型 start -->
          <el-form-item label="校验类型" prop="inspectionType" :rules="[
          { required: true, message: '校验类型不能为空'},
      { max: 50, message: '校验类型长度不能大于50个字符'}
    ]">
            <el-input type="text" v-model="validateForm.inspectionType" autocomplete="off" placeholder="请输入校验类型"></el-input>
          </el-form-item>
          <!-- 校验类型 end -->

          <!-- 创建时间 start -->
          <!-- <el-form-item label="创建时间" prop="createTime">
            <el-input type="text" v-model="validateForm.createTime" autocomplete="off" placeholder="请输入创建时间"></el-input>
          </el-form-item> -->
          <!-- 创建时间 end -->

          <!-- 备注 start -->
          <el-form-item label="备注" prop="notes">
            <el-input type="text" v-model="validateForm.notes" autocomplete="off" placeholder="请输入备注"></el-input>
          </el-form-item>
          <!-- 备注 end -->
        </el-form>
        <!-- form表单 -->

        <!-- 提交 start -->
        <div class="submit">
          <el-button type="primary" @click="addSubmitForm('validateForm')">提交</el-button>
          <el-button @click="addDialogClose">取消</el-button>
        </div>
        <!-- 提交 end -->
      </el-dialog>
      <!-- 新增 dialog对话框 end -->

      <!-- 编辑dialog对话框 start -->
      <el-dialog class="dialog" :title="dialogTitle" :visible.sync="editDialogVisible" :before-close="editHandleClose" :close-on-click-modal="false" fullscreen>
        <!-- 具体检测项 start -->

        <!-- 编辑表单 start -->
        <div class="edit-form">
          <!-- form表单 -->
          <el-form :model="validateForm" ref="validateForm" label-width="100px" class="demo-ruleForm" :rules="validateFormRules">
            <!-- 校验标准名称 start -->
            <el-form-item label="校验标准名称" prop="name">
              <el-input type="text" v-model="validateForm.name" autocomplete="off" placeholder="请输入校验标准名称"></el-input>
            </el-form-item>
            <!-- 校验标准名称 end -->

            <!-- 适用机器型号 start -->
            <el-form-item label="适用机器型号" prop="aplicableModel">
              <el-input type="text" v-model="validateForm.aplicableModel" autocomplete="off" placeholder="请输入适用机器型号"></el-input>
            </el-form-item>
            <!-- 适用机器型号 end -->

            <!-- 校验类型 start -->
            <el-form-item label="校验类型" prop="inspectionType" :rules="[
          { required: true, message: '校验类型不能为空'},
      { max: 50, message: '校验类型长度不能大于50个字符'}
    ]">
              <el-input type="text" v-model="validateForm.inspectionType" autocomplete="off" placeholder="请输入校验类型"></el-input>
            </el-form-item>
            <!-- 校验类型 end -->

            <!-- 创建时间 start -->
            <!-- <el-form-item label="创建时间" prop="createTime">
            <el-input type="text" v-model="validateForm.createTime" autocomplete="off" placeholder="请输入创建时间"></el-input>
          </el-form-item> -->
            <!-- 创建时间 end -->

            <!-- 备注 start -->
            <el-form-item label="备注" prop="notes">
              <el-input type="text" v-model="validateForm.notes" autocomplete="off" placeholder="请输入备注"></el-input>
            </el-form-item>
            <!-- 备注 end -->
          </el-form>
          <!-- form表单 -->

          <!-- 提交 start -->
          <div class="submit">
            <el-button type="primary" @click="addSubmitForm('validateForm')">提交</el-button>
            <!-- <el-button @click="addDialogClose">取消</el-button> -->
          </div>
          <!-- 提交 end -->
        </div>
        <!-- 编辑表单 end -->

        <el-table class="specific-detection-table" :data="checkTableData" style="width: 100%" border>
          <el-table-column v-for="(value, key) in checkTableFields" :key="key" :prop="value" :label="key" min-width="130">
          </el-table-column>
          <el-table-column label="操作" min-width="130" fixed="right">
            <template slot-scope="scope">
              <el-button @click.native.prevent="checkEditRow(scope.$index, scope.row)" type="text" size="small">
                编辑
              </el-button>

              <el-button @click.native.prevent="checkDeleteRow(scope.$index, scope.row)" type="text" size="small">
                删除
              </el-button>
            </template>
          </el-table-column>
        </el-table>

        <el-dialog class="inner-dialog" :title="innerDialogTitle" :visible.sync="innerVisible" append-to-body :before-close="checkHandleClose" :close-on-click-modal="false" center>
          <!-- 具体检测项表单 start -->
          <el-form :model="checkValidateForm" ref="checkValidateForm" label-width="100px" class="demo-ruleForm" :rules="checkValidateFormRules">
            <!-- 检测项 start -->
            <el-form-item label="检测项名称" prop="project">
              <el-input type="text" v-model="checkValidateForm.project" autocomplete="off" placeholder="请输入检测项"></el-input>
            </el-form-item>
            <!-- 检测项 end -->

            <!-- 技术参数 start -->
            <el-form-item label="技术参数" prop="parameter">
              <el-input type="text" v-model="checkValidateForm.parameter" autocomplete="off" placeholder="请输入技术参数"></el-input>
            </el-form-item>
            <!-- 技术参数 end -->
          </el-form>
          <!-- 具体检测项表单 end -->

          <!-- 内层提交 start -->
          <div slot="footer" class="submit">
            <el-button type="primary" @click="innerSubmitForm('checkValidateForm')">提交</el-button>
            <el-button @click="innerDialogClose">取消</el-button>
          </div>
          <!-- 内层提交 end -->
        </el-dialog>
        <!-- 具体检测项 end -->
        <!-- 提交 start -->
        <div class="check-submit">
          <el-button type="primary" @click="addCheckItem">新增</el-button>
          <el-button @click="checkDialogClose">关闭</el-button>
        </div>
        <!-- 提交 end -->
      </el-dialog>
      <!-- 编辑dialog对话框 end -->

    </el-container>
  </div>
</template>

<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'
export default {
  name: 'QualityControl',
  data() {
    /**
   * 公共验证函数(验证数字值)
   * @params message - 提示信息
   */
    function checkNumberMethod(name = "") {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback();
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'));
        } else {
          callback();
        }
      }
    }

    /**
    * 公共验证函数(验证必填数字值)
    * @params message - 提示信息
    */
    function checkRequireNumberMethod(name = "") {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'));
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'));
        } else {
          callback();
        }
      }
    }

    /**
    * 公共验证函数(验证必填值)
    * @params message - 提示信息
    */
    function checkRequireMethod(name = "") {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'));
        } else {
          callback();
        }
      }
    }

    return {
      feeInput: '', // 请输入手续费
      taxInput: '', // 请输入税点
      searchFeeInput: "", // 搜索的手续费
      searchTaxInput: "", // 搜索的税点

      tableLoading: false,
      tableFields: { //导出Excel表格的表头设置
        "校验标准名称": "name",
        "适用机器型号": "aplicableModel",
        "校验类型": "inspectionType",
        "创建时间": "createTime",
        "备注": "notes",
      },
      tableData: [], // 表格数据

      checkTableFields: { //导出Excel表格的表头设置
        "检测项": "project",
        "技术参数": "parameter",
      },
      checkTableData: [{
        project: "刹车",
        parameter: "1.2m",
      }, {
        project: "副刹",
        parameter: "1.2m",
      }], // 表格数据

      dateStateOptions: [{
        value: '1',
        label: '按月查询'
      }, {
        value: '2',
        label: '按日查询'
      }, {
        value: '3',
        label: '按年查询'
      }], // 日期统计方式选项

      currentPage: 1, // 当前页数,
      pageSize: 10, // 每页显示条数

      addDialogVisible: false, // 是否显示新增dialog对话框
      editDialogVisible: false, // 是否显示编辑dialog对话框
      dialogTitle: "", // dialog对话框的标题
      innerVisible: false, // 是否显示内层dialog对话框
      innerDialogTitle: "", // 内部dialog对话框的标题

      dialogState: "", // dialog是新增还是编辑表单，新增为'add'，编辑为'edit'
      innerDialogState: "", // 内部dialog是新增还是编辑表单，新增为'add'，编辑为'edit'

      selectDateState: "1", // 日期统计方式（月，日，年）
      dateType: "monthrange", // 日期类型（月，日，年）
      valueFormat: "yyyy-MM",

      searchValidateForm: {
        name: "", // 检验项名称
        startEndDate: "", // 起止日期
      }, // 搜索表单验证对象
      searchVF: {
        name: "", // 检验项名称
        startEndDate: "", // 起止日期
      }, // 提交表单验证对象
      validateForm: {}, // 表单验证对象
      checkValidateForm: {}, // 具体检测项表单验证对象

      searchSelectDateState: "1", // 搜索的日期统计方式
      searchStartEndDate: "", // 搜索的起止时间

      total: 0,

      standardId: "", // 当前具体检测项id

      // 提交表单验证规则
      validateFormRules: {
        // 校验标准名称
        name: [
          { validator: checkRequireMethod('校验标准名称'), required: true, trigger: ['blur', 'change'] }
        ],

        // 适用机器型号
        aplicableModel: [
          { validator: checkRequireMethod('适用机器型号'), required: true, trigger: ['blur', 'change'] }
        ],

        // 校验类型
        inspectionType: [
          { validator: checkRequireMethod('校验类型'), required: true, trigger: ['blur', 'change'] }
        ],
      },

      // 搜索表单验证规则
      searchValidateFormRules: {

      },

      // 质检标准具体检测项表单验证规则
      checkValidateFormRules: {
        // 检测项
        project: [
          { validator: checkRequireMethod('检测项'), required: true, trigger: ['blur', 'change'] }
        ],

        // 技术参数
        parameter: [
          { validator: checkRequireMethod('技术参数'), required: true, trigger: ['blur', 'change'] }
        ],
      }
    }
  },
  created() {
    // this.login()
    this.getValidateForm()
    this.getCheckValidateForm()
    this.initTableData()
  },
  mounted() {

  },
  methods: {
    // 获取表单验证对象
    getValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ""
      }
      this.validateForm = validateForm
      console.log(this.validateForm);
    },

    // 获取具体检测页面表单验证对象
    getCheckValidateForm() {
      var checkValidateForm = {}
      for (var item in this.checkTableFields) {
        checkValidateForm[this.checkTableFields[item]] = ""
      }
      this.checkValidateForm = checkValidateForm
      // console.log(this.checkValidateForm);
    },

    // 登录(必须)
    async login() {
      const { data: res } = await this.$http.post("/loginSystem?username=lijiazhao&password=lijiazhao")
    },

    /**
    * 初始化表格数据
    */
    async initTableData() {
      var name = this.searchVF.name
      if (name == "" || !name) {
        name = null
      }

      var startEndDate = this.searchVF.startEndDate
      var beginDate, endDate
      if (startEndDate == "" || !startEndDate) {
        // beginDate = formatMonth()
        // endDate = formatMonth()
        beginDate = null
        endDate = null
      } else {
        beginDate = this.searchVF.startEndDate[0]
        endDate = this.searchVF.startEndDate[1]
      }

      this.tableLoading = true

      const { data: res } = await this.$http.get("/system/srqis/list", {
        params: {
          name: name,
          beginTime: beginDate,
          endTime: endDate,
          pageNum: (this.currentPage - 1) * this.pageSize, // 页码
          pageSize: this.pageSize // 每页条数
        }
      })
      // console.log(res);
      this.tableData = res.data.list
      this.total = res.data.total

      this.tableLoading = false
    },

    /**
    * 新增最外层表格数据
    */
    async addTableData() {
      const { data: res } = await this.$http.post("/system/srqis/add", this.validateForm)
      if (res.state == "200") {
        this.$message({
          message: res.msg,
          type: 'success',
          offset: 100,
          center: true,
          duration: 1000,
        });
        this.initTableData()
        this.$refs.validateForm.resetFields() // 重置表单
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000,
        });
      }
    },

    /**
     * 修改最外层表格数据
     */
    async editTableData() {
      const { data: res } = await this.$http.post("/system/srqis/edit", this.validateForm)
      if (res.state == "200") {
        this.$message({
          message: res.msg,
          type: 'success',
          offset: 100,
          center: true,
          duration: 1000,
        });
        this.initTableData()
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000,
        });
      }
    },

    /**
     * 删除最外层表格数据
     * @params id - 要删除的质检标准id
     */
    async deleteTableData(id) {
      const { data: res } = await this.$http.delete("/system/srqis/delete", {
        params: {
          id: id
        }
      })
      if (res.state == "200") {
        this.$message({
          message: res.msg,
          type: 'success',
          offset: 100,
          center: true,
          duration: 1000,
        });
        this.initTableData()
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000,
        });
      }
    },

    /**
     * 获取具体检测项列表
     */
    async getShowDetail() {
      const { data: res } = await this.$http.get("/system/srqis/showDetail", {
        params: {
          id: this.standardId
        }
      })
      // console.log(res);
      this.checkTableData = res.data
    },

    /**
    * 新增具体检测项表格数据
    * @params standardId - 质检标准id
    */
    async addDetailTableData() {
      const { data: res } = await this.$http.post("/system/srqis/addDetail", {
        ...this.checkValidateForm,
        standardId: this.standardId, // 质检标准id
        serial: this.checkTableData.length + 1// 序号
      })
      if (res.state == "200") {
        this.$message({
          message: res.msg,
          type: 'success',
          offset: 100,
          center: true,
          duration: 1000,
        });
        this.getShowDetail()
        this.$refs.validateForm.resetFields() // 重置表单
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000,
        });
      }
    },

    /**
     * 修改具体检测项表格数据
     */
    async editDetailTableData() {
      const { data: res } = await this.$http.post("/system/srqis/editDetail", {
        ...this.checkValidateForm,
        standardId: this.standardId, // 质检标准id
      })
      if (res.state == "200") {
        this.$message({
          message: res.msg,
          type: 'success',
          offset: 100,
          center: true,
          duration: 1000,
        });
        this.getShowDetail()
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000,
        });
      }
    },

    /**
     * 删除具体检测项表格数据
     * @params id - 要删除的具体检测项中某一项的id
     */
    async deleteDetailTableData(id) {
      const { data: res } = await this.$http.delete("/system/srqis/deleteDetail", {
        params: {
          id: id,
          standardId: this.standardId, // 质检标准id
        }
      })
      if (res.state == "200") {
        this.$message({
          message: res.msg,
          type: 'success',
          offset: 100,
          center: true,
          duration: 1000,
        });
        this.getShowDetail()
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000,
        });
      }
    },

    // 选项值发生变化时触发
    onDateChange() {
      console.log(this.selectDateState);
      if (this.selectDateState == "1") { // 按月查询
        this.dateType = "monthrange"
        this.valueFormat = "yyyy-MM"
      } else if (this.selectDateState == "2") { // 按日查询
        this.dateType = "daterange"
        this.valueFormat = "yyyy-MM-dd"
      } else if (this.selectDateState == "3") { // 按年查询
        this.dateType = "yearrange"
        this.valueFormat = "yyyy"
      }
    },

    /**
     * 查询公司名称获取列表数据
     */
    getTableData() {
      // 如果输入框中的内容等于上次搜索的内容，则直接返回
      if (this.searchValidateForm.name == this.searchVF.name && this.selectDateState == this.searchSelectDateState && this.searchValidateForm.startEndDate == this.searchVF.startEndDate) {
        return
      }

      // 保存当前搜索的内容
      this.searchVF.name = this.searchValidateForm.name
      this.searchSelectDateState = this.selectDateState
      this.searchVF.startEndDate = this.searchValidateForm.startEndDate

      // 否则调用接口，请求数据，为tableData重新赋值
      console.log("调用接口");
      this.initTableData()
    },

    // 点击搜索按钮时触发
    onSearch(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // console.log(this.searchValidateForm);
          this.getTableData()
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },

    // 重置
    onReset() {
      // 清空所有输入框内容
      this.searchValidateForm.name = ""
      this.searchValidateForm.startEndDate = ""
      this.selectDateState = "1"
      this.dateType = "monthrange"

      // 重新获取表格数据
      this.getTableData()
    },

    // 点击外层添加时触发
    onAdd() {
      this.addDialogVisible = true
      this.dialogState = "add"
    },

    // 点击外层编辑时触发
    editRow(index, row) {
      console.log(index, row);
      this.editDialogVisible = true // 是否显示编辑dialog对话框

      this.dialogTitle = row.name
      this.editDialogTitle = row.name

      var rowObj = Object.assign({}, row) // 深拷贝row对象
      this.validateForm = rowObj
      this.dialogState = "edit"

      // 保存当前具体检测项id
      this.standardId = row.id

      this.getShowDetail()
    },

    // 点击外层删除时触发
    deleteRow(index, row) {
      this.$confirm('是否删除该行数据', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deleteTableData(row.id)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除',
          offset: 100,
          center: true,
          duration: 1000,
        });
      })
    },

    // 点击具体检测项内的编辑时触发
    checkEditRow(index, row) {
      console.log(index, row);
      this.innerVisible = true
      this.innerDialogTitle = row.project
      var rowObj = Object.assign({}, row) // 深拷贝row对象
      this.checkValidateForm = rowObj
      this.innerDialogState = "edit"
      this.$refs.checkValidateForm.resetFields(); // 重置表单
    },

    // 内层dialog新增按钮
    addCheckItem() {
      this.innerVisible = true
      this.innerDialogState = "add"
    },

    // 点击新增dialog右上角关闭按钮触发
    addHandleClose() {
      this.addDialogVisible = false
      this.$refs.validateForm.resetFields()  // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // dialog 关闭按钮
    addDialogClose() {
      this.addDialogVisible = false
      this.$refs.validateForm.resetFields(); // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // 新增dialog提交
    addSubmitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.dialogState == "add") {
            // 新增公司信息
            console.log("新增质检标准");
            this.addTableData()
            this.addDialogVisible = false
            this.$refs.validateForm.resetFields(); // 重置表单
          } else if (this.dialogState == "edit") {
            // 编辑公司信息
            console.log("编辑质检标准");
            this.editTableData()
          }


        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },

    // 点击具体检测项内的删除时触发
    checkDeleteRow(index, row) {
      this.$confirm('此操作将删除该具体检测项, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deleteDetailTableData(row.id)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除',
          offset: 100,
          center: true,
          duration: 1000,
        });
      });
    },

    // 编辑dialog 右上角关闭按钮
    editHandleClose(done) {
      done() // 关闭dialog对话框
      this.$refs.validateForm.resetFields(); // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // （编辑）具体检测项dialog 取消
    checkDialogClose() {
      this.editDialogVisible = false
      // this.$refs.validateForm.resetFields(); // 重置表单（清空无效）
      // this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // 内层 dialog 取消
    innerDialogClose() {
      this.innerVisible = false
      this.$refs.checkValidateForm.resetFields(); // 重置表单（清空无效）
      this.getCheckValidateForm() // 将checkValidateForm对象中各个属性清空
    },

    // 点击内层dialog 右上角关闭按钮触发
    checkHandleClose(done) {
      done()
      this.$refs.checkValidateForm.resetFields(); // 重置表单（清空无效）
      this.getCheckValidateForm() // 将checkValidateForm对象中各个属性清空
    },

    // 内层dialog提交
    innerSubmitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          console.log(this.checkValidateForm);
          if (this.innerDialogState == "add") {
            // 新增公司信息
            console.log("新增具体检测项");
            this.addDetailTableData()
          } else if (this.innerDialogState == "edit") {
            // 编辑公司信息
            console.log("编辑具体检测项");
            this.editDetailTableData()
          }
          this.innerVisible = false
          this.$refs.checkValidateForm.resetFields(); // 重置表单
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },

    // currentPage 改变时会触发
    onCurrentChange(currentPage) {
      this.currentPage = currentPage
      this.initTableData()
    },

    // pageSize 改变时会触发
    onSizeChange(pageSize) {
      // console.log(pageSize);
      this.pageSize = pageSize
      this.initTableData()
    },
  },
  components: {
    // DownloadExcel: JsonExcel,
  },
  computed: {
  }
}
</script>

